canvas {
  position: absolute;
  top: 0;
  z-index:-1;
  margin-top: -6.5%;
}

html, body {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  position: relative;
  top: 50% !important;
  -webkit-transform: translateY(-50%) !important;
      -ms-transform: translateY(-50%) !important;
          transform: translateY(-50%) !important;
}

.noselect {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.fade-in {
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

h1, h2, h3, h4, h5, span, p, .extra, .intro{
  cursor: default;
}

.intro.row {
  height: 100px;
  line-height: 50px;
}

svg {
  height: 100px;
  width: 100px;
}

.intro {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 4em;
  margin-bottom: 10px;
}

@media only screen and (max-width: 768px){
  .intro {
    font-size: 3em !important;
  }
  .intro.row {
    line-height: 40px;
  }
}

/* Smartphones (portrait) ----------- */
@media only screen and (max-width: 414px) {
  .intro {
    margin-top: 10px !important;
    height: 70px !important;
    font-size: 2em !important;
  }
  svg {
    height: 75px;
    width: 75px;
  }
}



