span.name, span.title {
  font-family: 'Roboto', serif;
  font-weight: 300;
  font-size: 7em;
  margin: 0 10px;
  line-height: 1em;
}

.pulse {
  height: 10px;
}
.pulse > h5 {
  color: rgba(0, 0, 0, 0.75);
}

@media only screen and (max-width: 768px){
  span.name, span.title {
    font-size: 5em;
    margin: 0 10px;
  }
}

@media only screen and (max-width: 414px) {
  span.name, span.title {
    font-size: 1.8em;
    margin: 0 5px;
  }
}

@media only screen and (max-width: 320px) {
  span.name, span.title {
    font-size: 1.55em;
    margin: 0 5px;
  }
}



